import React from 'react';

export default class Popup extends React.Component {

state = {
  identity: "mood-9",
  show: "no",
  text: null
}

  changeMood = (e) => {
    this.state.identity = e
    this.props.onChange(this.state.identity); // Draws from the state
  };

  // textChange = (e) => {
  //   this.state.identity = e
  //   this.props.textChange(this.state.text); // Draws from the state
  // };

  closePopup() {
    this.props.close(this.state.show);
    console.log("blah")
  }

  handleSubmit = (e) => {
    e.preventDefault();
    // let notes = textarea.value
    this.props.addMarker(this.state);
    this.closePopup()
  }

  handleChange = (e) => {
    this.setState({
    text: e.target.value
    })
    this.props.textChange(this.state.text);

  }

  render() {

    return (
    <div id={this.props.toggle} className="modal">
    <span className="closeBtn" onClick={ this.closePopup.bind(this) }>&times;</span>
    <form onSubmit={this.handleSubmit}>
    Hi Richard, how are you feeling today?
    <p>
    <input type="button" onClick={() => this.changeMood("mood-1")} className="mood-button" id= "mood-1"/>
    <input type="button" onClick={() => this.changeMood("mood-2")} className="mood-button" id="mood-2"/>
    <input type="button" onClick={() => this.changeMood("mood-3")} className="mood-button" id="mood-3"/>
    <input type="button" onClick={() => this.changeMood("mood-4")} className="mood-button" id="mood-4"/>
    <input type="button" onClick={() => this.changeMood("mood-5")} className="mood-button" id="mood-5"/>
    <input type="button" onClick={() => this.changeMood("mood-6")} className="mood-button" id="mood-6"/>
    <input type="button" onClick={() => this.changeMood("mood-7")} className="mood-button" id="mood-7"/>
    </p>
    <p><textarea name="notes" placeholder="Do you have any notes?" onChange={this.handleChange}></textarea></p>
    <p><input type="submit" name="submit" value="Submit!" /></p>
    </form>
    </div>
  )
}
}
